.nav {
  @media (min-width: @desktop-width) {
    display: flex;
    align-items: flex-end;
  }
}

.nav__wrapper {
  position: absolute;
  width: 322px;
  left: -251px;
  top: 65px;


  @media (min-width: @tablet-width) {
    display: block;
    position: static;
    width: auto;
  }

  @media (min-width: @desktop-width) {
    display: block;
    position: static;
    max-width: 500px;
  }
}
